.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 720px;
  background: var(--background);
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04),
    0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  position: relative;
  text-align: center;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  cursor: pointer;
  background: transparent;
  border: 0;
  width: 24px;
  height: 24px;
  object-fit: cover;
  padding: 0;
  transition: 0.2s;
}

.button_a {
  position: absolute;
  top: 40px;
  right: 40px;
}

.button:hover {
  opacity: 0.6;
}
